import React, { useState, Component } from "react";
import { getGroupedOffers } from "../../helpers/api";

export const CardWinning = () => {
    const [count, setCount] = useState("--");

    getGroupedOffers("winning")
        .then(function (products) {
            setCount(products.length);
        })
        .catch(function (error) {
            return error;
        });

    return (
        <a class="card bg-light mb-3 stretched-link">
            <div class="card-header">Winning Offers</div>
            <div class="card-body text-success">
                <h5 class="card-title counter-total">{count}</h5>
            </div>
        </a>
    );
};

